.about-couple-card {
  display: flex;
  justify-content: space-between;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  margin: 20px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about-section {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.about-section.groom {
  border-right: 1px solid #ddd;
}

.about-section.bride {
  border-left: 1px solid #ddd;
}

.about-text {
  flex: 1;
  margin-right: 20px;
}

.about-text h2 {
  font-family: 'Playfair Display', serif;
  font-size: 2em;
  color: #D35400;
}

.about-text p {
  font-size: 1.1em;
  color: #555;
  line-height: 1.6;
}

.about-image img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .about-couple-card {
    flex-direction: column;
  }

  .about-section {
    flex-direction: column;
    border: none;
    padding: 20px 0;
    text-align: center;
  }

  .about-section.groom {
    border-bottom: 1px solid #ddd;
  }

  .about-section.bride {
    border-top: 1px solid #ddd;
  }

  .about-image img {
    width: 120px;
    height: 120px;
  }
}
